let _methods = {
  //10000  1
  numberUnit(value) {
    let num = "";
    if (value > 9999) {
      num = Math.floor(value / 1000) / 10 + "";
    } else if (value < 9999 && value > -9999) {
      num = value;
    }
    return num;
  },

  //
  tanChuang(that, value) {
    console.log(value);
    let message = that.$message({
      message: value,
      offset: 100,
      showClose: true,
      type: "error"
    });
    return message;
  },

  //
  tanChuangOk(that, value) {
    console.log(value);
    let message = that.$message({
      message: value,
      offset: 100,
      showClose: true,
      type: "success"
    });
    return message;
  }

};

class Storage {
  constructor(name) {
    this.name = "storage";
    console.log(name);
  }
  //
  setItem(params) {
    let obj = {
      name: '',
      value: '',
      expires: 1800000,
      // expires: 3000,
      startTime: new Date().getTime()//
    }
    let options = {};
    //objparams
    Object.assign(options, obj, params);
    if (options.expires) {
      //options.expires
      //options.namekeyoptions
      localStorage.setItem(options.name, JSON.stringify(options));
    } else {
      //options.expiresvalue
      let type = Object.prototype.toString.call(options.value);
      console.log(type);
      //valueJSON.stringify
      if (Object.prototype.toString.call(options.value) == '[object Object]') {
        options.value = JSON.stringify(options.value);
      }
      if (Object.prototype.toString.call(options.value) == '[object Array]') {
        options.value = JSON.stringify(options.value);
      }
      localStorage.setItem(options.name, options.value);
    }
  }
  //
  getItem(name) {
    let item = localStorage.getItem(name) || "";
    //json
    try {
      item = JSON.parse(item);
    } catch (error) {
      //json
      item;
    }
    //startTime
    if (item.startTime) {
      let date = new Date().getTime();
      //item.expires
      if (date - item.startTime > item.expires) {
        //false
        localStorage.removeItem(name);
        return false;
      } else {
        //
        return item.value;
      }
    } else {
      //
      return item;
    }
  }
  //
  removeItem(name) {
    localStorage.removeItem(name);
  }
  //
  clear() {
    localStorage.clear();
  }
}

export { circleListsData, _methods, Storage };
